@use "global"as *;

.top-campaign {
  overflow: hidden;
}

.top-campaign__inner {
  width: 100%;
  height: 100%;
  position: relative;
}

.top-campaign__heading {}

.top-campaign__cards-wrapper {
  width: 100vw;
  height: 100%;
  margin-top: rem(28);
  padding-bottom: rem(4);


  @include mq(md) {
    margin-top: rem(40);
    margin-right: calc(50% - 50vw);
  }
}

//swiper-wrapper
.top-campaign__cards {
  width: 100vw;
  height: 100%;
}

.top-campaign__button {
  margin-top: rem(40);
	margin-inline:auto;
	text-align: center;
  padding-bottom: rem(4);

  @include mq(md) {
    margin-top: rem(72);
  }
}
