@use "global" as *;

.top-blog {
	position: relative; /*  */
  background-color: white; /*  */
	padding-top: rem(80);
		@include mq(md) {
			padding-top: rem(96);
	}
}

.top-blog::before {
	@include mq(md) {
		content:"";
		position: absolute; /*  */
    top: 0; /* 0px */
		background-image: url('../images/blog-mv-pc.jpg'); /*  */
    width: 100%;   /*  */
    height: rem(500);  /* 500 */
    background-size: cover; /*  */
    background-position: center; /*  */
		z-index: 1; /* z-index */
	}
}

.top-blog__inner {
	padding-left: rem(21);
	padding-right: rem(21);
	position: relative; /*  */
  z-index: 2; /*  */
}

.top-blog__fish-image {
display: none;
@include mq(md) {
	display: block;
	position: absolute;
	z-index:100;
	top:rem(-72);
	left: calc(50% - min(48vw, 38.75rem));
	max-width: rem(437);
}
}
.top-blog__fish-image img {
	width: 100%;
	aspect-ratio: 437 / 201;
	object-fit: cover;
}
.top-blog__cards {
	margin-inline:auto;
	width:100%;
	margin-top: rem(26);

	@include mq(md) {
		margin-top: rem(80);
	}
}
.top-blog__button {
	margin-top: rem(36);
	text-align: center;
	padding-bottom: rem(4);
	@include mq(md) {
		margin-top: rem(78);
	}
}
