@use "global"as *;

//
/*  */
.wpcf7-form-control-wrap {
  margin-top: 16px;
}

@media (min-width: 768px) {
  .wpcf7-form-control-wrap {
    margin-top: 0;
  }
}

/*  */
.wpcf7-form-control.wpcf7-checkbox {
  position: relative;
  display: block;
  /*  */
  padding-left: 10px;
  cursor: pointer;
  user-select: none;
  line-height: 1.7;
}

/*  input  */
.wpcf7-form-control.wpcf7-checkbox input[type="checkbox"] {
  display: none;
}

/*  */
.wpcf7-form-control.wpcf7-checkbox input[type="checkbox"]+.wpcf7-list-item-label::before {
  content: "";
  position: absolute;
  top: 50%;
  /*  */
  transform: translateY(-50%);
  left: 0;
  height: 14px;
  /*  */
  width: 14px;
  border: solid 2px #4CAF50;
  /*  $greenaccent  */
  box-sizing: border-box;
}

/*  */
.wpcf7-form-control.wpcf7-checkbox input[type="checkbox"]+.wpcf7-list-item-label--error::before {
  border-radius: 50%;
}

/*  */
.wpcf7-form-control.wpcf7-checkbox input[type="checkbox"]:checked+.wpcf7-list-item-label::after {
  content: "";
  position: absolute;
  width: 4px;
  height: 8px;
  top: 50%;
  left: 3px;
  transform: translateY(-50%) rotate(45deg);
  border-right: 2px solid #c94800;
  /*  */
  border-bottom: 2px solid #c94800;
  /*  */
  opacity: 1;
  /*  */
}

/*  */
.wpcf7-form-control.wpcf7-checkbox input[type="checkbox"]:checked+.wpcf7-list-item-label--error::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #408F95;
  /*  */
  opacity: 1;
  /*  */
}



//
/*  */
.form__select {
  margin-top: rem(16);

  @include mq(md) {
    margin-top: 0;
  }
}

/* div */
.form__select-items {
  display: inline-block;
  position: relative;
  width: 100%;
  height: rem(40);

  /* pxrem */
  @include mq(md) {
    height: rem(30);
    /*  */
  }

  /* Contact Form 7 */
  .wpcf7-form-control-wrap {
    width: 100%;
    /*  */
  }
}

/*  */
.form__select-items::after {
  content: "";
  position: absolute;
  right: 8px;
  /*  */
  top: 50%;
  /*  */
  transform: translateY(-50%);
  /* Y */
  width: 12px;
  height: calc(12px / 2 * tan(60deg));
  /*  */
  clip-path: polygon(0 0, 100% 0, 50% 100%);
  /*  */
  background: $greenaccent;
  /*  */
}

/*  */
.form__select-items.no-triangle::after {
  display: none;
}

/*  */
.form__select-item,
.wpcf7-select {
  appearance: none;
  /*  */
  -webkit-appearance: none;
  /* Chrome/Safari */
  -moz-appearance: none;
  /* Firefox */
  background-color: white;
  /*  */
  border: 1px solid $greenaccent;
  /*  */
  width: 100%;
  /*  */
  height: inherit;
  /*  */
  padding-left: rem(8);
  /*  */
  font-size: rem(12);
  /*  */
  font-family: $base-font-family;
  /*  */
  font-weight: $regular;
  /*  */
  line-height: 3;
  letter-spacing: 0;
  /*  */
}

/*  */
.form__row.form__row--select {
  @include mq(md) {
    padding-top: rem(34);
    /*  */
  }
}

/* dt */
.form__row.form__row--select dt {
  @include mq(md) {
    margin-top: rem(4);
    /*  */
  }
}



/*  */
.wpcf7-form-invalid .wpcf7-form-control.wpcf7-not-valid {
  background-color: rgba(255, 0, 0, 0.1);
  /*  */
}

/*  */
.wpcf7-form-invalid input::placeholder,
.wpcf7-form-invalid textarea::placeholder {
  color: #d9534f;
  /*  */
}

/*  */
.wpcf7-form-invalid span.wpcf7-not-valid-tip {
  color: #d9534f;
  /*  */
}


//
/**/
input[type="submit"] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

span.wpcf7-spinner {
  display: none;
}



